/*  */
.note-container {
    padding-left: 10%;      /* 10% */
    padding-right: 10%;
}

/*  */
.note-item {
    margin-bottom: 0.7rem;  /* 0.7rem */
}

/*  */
.block {
    display: block;         /*  */
}

/*  */
.font-bold {
    font-weight: bold;      /*  */
}

/*  */
.mb-2 {
    margin-bottom: 0.5rem;  /* 0.5rem */
}

/*  */
.text-left {
    text-align: left;       /*  */
}

/*  */
.w-full {
    width: 100%;            /*  */
}

/*  */
.input-textarea {
    width: 100%;                    /*  */
    min-height: 90px;              /* 90px3 */
    border: 0.5px solid #dcdcdc;      /* 1px */
    border-radius: 5px;             /* 4px */
    padding: 8px;                   /* 8px */
    box-shadow: 0 0 5px rgba(0,0,0,0.1); /*  */
    resize: vertical;               /*  */
    box-sizing: border-box;         /*  border-box padding  border  */
}

/* placeholder */
.input-textarea::placeholder {
    color: #999999;                 /*  */
    text-align: left;               /*  */
}
