/*  */
.game-info-container {
    padding-top: 30px;      /* 30px */
    padding-left: 10%;      /* 10% */
    padding-right: 10%;
}

/*  */
.game-info-item {
    margin-bottom: 0.7rem;  /* 0.7rem */
}

/*  */
.game-info-row {
    display: flex;                /*  */
    justify-content: space-between; /*  */
    margin-bottom: 0.7rem;        /* 0.7rem */
}

/*  */
.game-info-column {
    display: flex;                /*  */
    flex-direction: column;       /*  */
    width: 48%;                   /* 48% */
}

/*  */
.game-info-column-left {
    margin-right: 4%;             /* 4% */
}

/*  */
.p-dropdown,
.p-inputtext {
    height: 38px;                 /* 38px */
}

/*  */
.p-inputtext {
    padding-left: 0px;            /* 0 */
}

/* placeholder */
.p-inputtext::placeholder {
    text-align: left;             /*  */
}

/*  */
.p-dropdown .p-dropdown-label {
    display: flex;                /*  */
    align-items: center;          /*  */
    justify-content: flex-start;  /*  */
}

/*  */
.game-info-score-input {
    width: 100% !important;       /*  !important  */
    text-align: left !important;  /*  !important  */
}

/*  */
.rounds-option {
    color: #4682B4;               /*  */
}
