/*  */
.scoreboard-container {
    display: flex;                /*  */
    flex-direction: column;       /*  */
    align-items: center;          /*  */
    padding: 0 10%;               /* 010% */
    width: 100%;                  /*  */
}

/*  */
.players-header {
    display: flex;                /*  */
    justify-content: space-between; /*  */
    width: 100%;                  /*  */
    margin-bottom: 0.5rem;          /* 1rem */
}

/*  */
.team-header {
    width: 100%;                  /*  */
    text-align: center;           /*  */
    margin-bottom: 10px;          /* 10px */
    padding: 8px;                 /* 8px */
    border-radius: 5px;           /* 5px */
}

/*  */
.team-header.our {
    background-color: #87CEEB;    /*  */
}

/*  */
.team-header.enemy {
    background-color: #FF6347;    /*  */
}

/*  */
.team-label {
    font-size: 16px;              /* 16px */
    font-weight: bold;            /*  */
    color: #FFFFFF;               /*  */
}

/*  */
.player-input {
    width: 45%;                   /* 45% */
}

/*  */
.player-spacer {
    width: 10%;                   /* 10% */
}

/*  */
.player-name-input {
    width: 100%;                  /*  */
    text-align: center !important;/*  !important  */
}

/* placeholder */
.player-name-input::placeholder {
    text-align: center !important;/*  !important  */
}

/*  */
.score-section {
    width: 100%;                  /*  */
    margin-bottom: 0.5rem;        /* 0.5rem */
    padding: 0.5rem;              /* 0.5rem */
    border-radius: 5px;           /* 5px */
}

/*  */
.category-label {
    font-weight: bold;            /*  */
    font-size: 1rem;              /* 1rem */
    text-align: center;           /*  */
    margin-bottom: 0.5rem;          /* 0.5rem */
}

/*  */
.score-row,
.special-row {
    display: flex;                /*  */
    justify-content: space-between; /*  */
    align-items: center;          /*  */
    margin-bottom: 0.5rem;        /* 0.5rem */
}

/*  */
.player-score {
    display: flex;                /*  */
    align-items: center;          /*  */
    justify-content: center;      /*  */
    width: 40%;                   /* 40% */
}

/* OX */
.success-label,
.fail-label {
    font-weight: bold;            /*  */
    font-size: 1rem;              /* 1rem */
}

/*  */
.special-row .category-label {
    flex: 1;                      /* 1 */
    text-align: center;           /*  */
    margin: 0 10px;               /* 10px */
}

/*  */
.special-row .player-score {
    flex: 2;                      /* 2 */
    display: flex;                /*  */
    justify-content: space-between; /*  */
    align-items: center;          /*  */
}

/*  */
.score-input {
    width: 40px !important;         /* 40px !important  */
    text-align: center !important;  /*  !important  */
}

/*  */
.score-button {
    width: 2rem !important;         /* 2rem !important  */
    height: 2rem !important;        /* 2rem !important  */
}

/*  */
.scoreboard-container .p-inputtext {
    padding-left: 0px !important;   /*  !important  */
    text-align: center !important;  /*  !important  */
}

/*  */
.p-button-secondary:hover {
    background-color: #e9ecef;    /*  */
    color: #495057;               /*  */
    border-color: #adb5bd;        /*  */
}

/*  */
.attack {
    background-color: #FFE0E0;    /*  */
}

/*  */
.defense {
    background-color: #E0FFE0;    /*  */
}

/*  */
.midfield {
    background-color: #E0E0FF;    /*  */
}

/*  */
.chance {
    background-color: #FFFFE0;    /*  */
}

/*  */
.smash {
    background-color: #FFE0FF;    /*  */
}
